.projects {
  background-color: #3d556d;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  overflow: hidden;
}

.cards-container {
  margin: 0px;
  padding: 3% 3% 3% 3%;
  max-width: 100%;
  max-height: 100%;
}

.cards-row {
  margin: 0px;
}

.cards-column {
  display: flex;
  justify-content: center;
  padding: 1% 1%;
}

.cards-item {
  display: flex;
  background-color: #ffffff;
  border: none;
  width: 100%;
  height: 100%;
  border: 1px #3d556d;
  box-shadow: 0.5vw 0.5vw 0.5vw -0.4vw #f8f9fa;
}

.cards-header {
  color: #ffffff;
  /* background-color: #609cc4; */
  background-color: #304a62;
  font-size: 2.3rem;
  height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cards-image-group {
  position: relative;
}

.cards-brands-logos {
  position: absolute;
  top: 50%;
  min-width: 100%;
  transform: translateY(-50%);
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.cards-brands-logos img {
  position: relative;
  width: 14%;
  margin-left: 3%;
  margin-right: 3%;
}

.cards-background-image {
  z-index: -1;
  filter: blur(0.5vh);
  -webkit-filter: blur(0.5vh);
  -moz-filter: blur(0.5vh);
  -o-filter: blur(0.5vh);
  -ms-filter: blur(0.5vh);
  padding: 0.5vh 0.5vh;
}

.cards-body {
  display: flex;
  flex-direction: column;
}

.cards-text {
  text-align: justify;
  text-justify: inter-word;
}

.cards-text-tail {
  margin-top: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.list-group-flush {
  font-size: 1.2rem;
  width: 60%;
}

.list-group-item {
  background-color: #f8f9fa;
}

.cards-item .fa-icon {
  margin-left: 4rem;
  color: rgb(83, 83, 83);
}

.cards-footer {
  background-color: #d8dcdf;
}
