.footer {
  overflow: hidden;
}

.footer .background_image {
  z-index: -1;
  position: absolute;
  left: 0px;
  bottom: 0px;
  object-fit: cover;
  /* max-width: 100%; */
  width: 100%;
  height: 100vh;
  /* opacity: 0.9; */
}

.icons-list {
  list-style: none;
  position: absolute;
  top: 10vh;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-wrap: nowrap;
}

.icons-list li {
  float: left;
}

.fa-icon {
  margin: 1.5vh;
  color: white;
  font-size: 3.5vh;
}

.footer-comment {
  white-space: nowrap;
  color: white;
  position: absolute;
  top: 25vh;
  font-size: 1.5vh;
  left: 50%;
  transform: translateX(-50%);
}

.react-logo {
  vertical-align: middle;
  height: 2.5vh;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .react-logo {
    animation: react-logo-spin infinite 20s linear;
  }
}

@keyframes react-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
